<template>
  <Loading :active="isLoading"></Loading>
  <div class="form row justify-content-center">
    <Form class="col-sm-12" v-slot="{ errors }" @submit="createOrder">
      <div class="mb-2">
        <label for="email" class="form-label">Email</label>
        <Field
          id="email"
          name="email"
          type="email"
          class="form-control"
          :class="{ 'is-invalid': errors['email'] }"
          placeholder=" Email"
          rules="email|required"
          v-model="form.user.email"
        ></Field>
        <ErrorMessage name="email" class="invalid-feedback"></ErrorMessage>
      </div>

      <div class="mb-2">
        <label for="name" class="form-label"></label>
        <Field
          id="name"
          name=""
          type="text"
          class="form-control"
          :class="{ 'is-invalid': errors[''] }"
          placeholder=""
          rules="required"
          v-model="form.user.name"
        ></Field>
        <ErrorMessage name="" class="invalid-feedback"></ErrorMessage>
      </div>

      <div class="mb-2">
        <label for="tel" class="form-label"></label>
        <Field
          id="tel"
          name=""
          type="tel"
          class="form-control"
          :class="{ 'is-invalid': errors[''] }"
          placeholder=""
          rules="required"
          v-model="form.user.tel"
        ></Field>
        <ErrorMessage name="" class="invalid-feedback"></ErrorMessage>
      </div>

      <div class="mb-2">
        <label for="address" class="form-label"></label>
        <Field
          id="address"
          name=""
          type="text"
          class="form-control"
          :class="{ 'is-invalid': errors[''] }"
          placeholder=""
          rules="required"
          v-model="form.user.address"
        ></Field>
        <ErrorMessage name="" class="invalid-feedback"></ErrorMessage>
      </div>

      <div class="mb-2">
        <label for="message" class="form-label"></label>
        <textarea
          name=""
          id="message"
          class="form-control"
          cols="5"
          rows="5"
          v-model="form.message"
        ></textarea>
      </div>
      <div class="d-flex justify-content-between">
        <button @click.prevent="backToCart" class="btn px-3 mt-3">&lt; </button>
        <button class="btn px-3 mt-3"> &gt;</button>
      </div>
    </Form>
  </div>
</template>
<script>
export default {
  data () {
    return {
      form: {
        user: {
          name: '',
          email: '',
          tel: '',
          address: ''
        },
        message: ''
      },
      isLoading: false
    }
  },
  methods: {
    createOrder () {
      const api = `${process.env.VUE_APP_API}api/${process.env.VUE_APP_PATH}/order`
      const order = this.form
      this.axios.post(api, { data: order }).then((res) => {
        const orderId = res.data.orderId
        this.$router.push(`order/${orderId}`)
      })
    },
    backToCart () {
      this.$router.push('/user')
    }
  },
  created () {
    this.isLoading = true
    setTimeout(() => {
      this.isLoading = false
    }, 300)
  }
}
</script>

<style scoped src="../../assets/css/front/userinfo.scss" lang="scss"></style>
