import { defineStore } from 'pinia'

// 1.store
// 2.
export default defineStore('user store', {
  // data
  state: () => {
    return {
      player: 'Melo',
      age: 37
    }
  },
  // computed
  getters: {
    getUserName: (state) => {
      return `:${state.player}`
    }
  },
  // methods , this
  actions: {
    updatePlayer () {
      this.player = 'James'
      this.age = 38
    }
  }
})
